#roundButton {
    background-color: #ececec;
    margin-bottom: 20px;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    border: 20px solid #000000;
    outline: none;
    transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
  }
   
  #roundButton:hover {
    background-color: #8a8a8a;
    cursor: pointer;
    border-color: #3b3b3b;
    transform: scale(2.2, 2.2);
  }
   
  #roundButton:active {
    border-color: #000000;
    background-color: rgb(0, 0, 0);
  }